import React from 'react';
import ReactDOM from 'react-dom';
import Modal from 'react-responsive-modal';
import { AwesomeButton } from "react-awesome-button";
import "react-awesome-button/dist/styles.css";
import axios from 'axios';

const styles = {
  fontFamily: "sans-serif",
  textAlign: "center"
};

export default class Forum extends React.Component {
  constructor() {
    super();
    this.state = {
      open: false,
      username: null,
      password: null,
      guest: null,
      userDisplay: true,
      guestDisplay: false,
      userID: null
    };
  }

  onOpenModal = () => {
    this.setState({ open: true });
  };

  onCloseModal = () => {
    this.setState({ open: false });
  };

  onSubmit(e) {
    e.preventDefault();
    console.log(this.state.username, this.state.password);
    const { username, password } = this.state;
    if(username && password) {
      axios.post('https://clubster-backend.herokuapp.com/api/login', { username, password }).then((response) => {
        this.setState({ userID: response.data._id});
      });
      axios.post('https://clubster-backend.herokuapp.com/api/login')
    }

    //add user to went array
  }

  _renderFormUser() {
     return (
       <form style = {{marginTop:20, textAlign: 'center', justifyContent: 'center', alignItems: 'center'}}>
        <p>
         <span className="input">
           <input type="text" placeholder="Username" onChange={ (e) => this.setState({ username: e.target.value }) } />
           <span></span>
         </span>
        </p>
        <p>
        	<span className="input">
        		<input type="text" placeholder="Password" onChange={ (e) => this.setState({ password: e.target.value }) } />
        		<span></span>
        	</span>
        </p>
         <button type='button' onClick= {this.onSubmit.bind(this)}>Submit</button>
       </form>
     )
  }

  _renderFormGuest() {
     return (
       <form style = {{marginTop:20, textAlign: 'center', justifyContent: 'center', alignItems: 'center'}}>
        <p>
         <span className="input">
           <input type="text" placeholder="Enter your name" onChange={ (e) => this.setState({ guest: e.target.value }) } />
           <span></span>
         </span>
        </p>
         <button type='button' onClick= {this.onSubmit.bind(this)}>Submit</button>
       </form>
     )
  }

  render() {
    const { open } = this.state;
    return (
      <div id= "App">
        <button onClick={this.openModal}>Open Modal</button>
        <div style={styles}>
          <h2>react-responsive-modal</h2>
          <button onClick={this.onOpenModal}>Open modal</button>
          <Modal open={true} onClose={this.onCloseModal} styles = {{backgroundColor: 'blue'}}center>
            <h2> Sign into the event! </h2>
            <AwesomeButton type="primary" onPress = {() => this.setState({userDisplay:true,guestDisplay:false})}>Sign in as User</AwesomeButton>&nbsp;
            <AwesomeButton type="primary" onPress = {() => this.setState({userDisplay:false,guestDisplay:true})}>Sign in as Guest</AwesomeButton>
            {(this.state.userDisplay) ? this._renderFormUser() : this._renderFormGuest() }
          </Modal>
        </div>
      </div>
    );
  }
}
